.saying{
    border: 5px solid #000000;
    height: 25px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;

    overflow: hidden;
    padding: 5px;
    input{
        height: 100%;
        font-size: 18px;
        width: 100%;
        border: none;
    }
}


